@import "../global.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @include mobile{
    overflow: hidden;
  }

  h1 {
    text-align: center;
    width: 100%;
    font-size: 50px;
    height: 100px;

    @include mobile {
      overflow: hidden;
    }

    @include mobile2 {
      font-size: 20px;
    }

  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;
    margin-left: 70px;

    @include mobile {
      overflow: hidden;
    }

    @include mobile2 {
      height: 50px;
    }
   
  }

  .container {
    margin-bottom: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    overflow: hidden;

    @include mobile {
       flex-direction: column;
       overflow: hidden;
    }

    @include mobile2 {
      flex-direction: row;
    }


    .item {
      height: 100%;
      margin: 10px 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      transition: all 0.5s ease;

      @include mobile {
        overflow: hidden;
      }

      .sites {
        display: flex;
        flex-direction: row;
        

        a {
          height: 90%;
          overflow: hidden;
          cursor: pointer;
          margin-right: 10px;
  
          .MuiSvgIcon-root {
            position: inherit;
          margin-bottom: 40px;
            z-index: 2;
            color: white;
            font-size: 50px;
            overflow: hidden;
  
            @include mobile {
              color:rgb(250, 50, 67);
                display: flex;
                margin-bottom: 10px;
                font-size: 50px;
                overflow: hidden;
            }

            @include mobile2 {
              font-size: 30px;
            }
  
          
          }
        }

      }
      

      h3 {
        font-size: 20px;
        color: white;
        overflow: hidden;

@include mobile {
    font-size: 15px;
}

@include mobile2 {
  color: rgb(250, 50, 67);
}

      }

      img {
        width: 90%;
        height: auto;
        object-fit: cover;
        z-index: 1;
        border-radius: 8px;

        @include mobile {
          width: 70%;
        }

        @include mobile2 {
          display: none;
        }
      }

      &:hover {
        background-color: rgba(250, 50, 67, 0.7);
        transform: scale(1.1);

        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
