@import "../global.scss";

.menu{
    width: 300px;
    height: 100vh;
    background-color: #FA3243;
    position: fixed;
    top: -100vh;
    right: 0px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.active{
        top: 70px;
    }

    ul {
        margin:0;
        padding:0;
        list-style: 0;
        font-size: 30px;
        font-weight: 300;
        color: white;
        width: 60%;
    }

    li {
        margin-bottom: 20px;
        a{
            font-size: inherit;
            color: inherit;
            text-decoration: none;
        }

        &:hover {
            font-weight: 500;
        }
    }
}