@import "../global.scss";

.slider {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  @include mobile2 {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .arrows {
    display: flex;
    margin-bottom: 30px;
    position: inherit;

    @include mobile {
      align-items: center;
      justify-content: center;
    }

    @include mobile2 {
      margin-top: 80px;
      margin-right: 10px;
      margin-left: 10px;
    }
  

    .MuiSvgIcon-root {
        width: 60px;
        height: 60px;
      font-size: 40px;
      margin-right: 50px;
      margin-bottom: 20px;
      background-color: #fa3243;
      border-radius: 50%;
      color:white;
      cursor: pointer;

      @include mobile {
        margin: 0px;
        margin-right: 5px;
      }

      @include mobile2 {
        width: 30px;
        height: 30px;
        font-size: 20px;
        margin-left: 5px;
      }

      &:hover{
        background-color: #bd656c
      }
    }
  }

  .slide {
    transform: translatex(-100vw);
    transition: all 0.5s ease;
  }
  .slide.active {
    transform: translatex(0vw);
    transition: all 0.5s ease;

    img {
      width: 500px;
      height: 500px;
      transition: all 0.5s ease;
      object-fit: cover;

      @include mobile2 {
        object-fit: contain;
      }

      
    }
  }
}
