@import "../global.scss";

.footer{
    display: flex;
    align-items: center;

    @include mobile{
        flex-direction: column;
        justify-content: center;
    }
    
    
    

    .MuiSvgIcon-root {
        font-size:  40rem;
        color: white;
        animation: heart 2s infinite ease-in-out;
        margin-left: 50px;

        @include mobile {
            font-size: 200px;
            margin: 0;
        }
        

        @keyframes heart {
            50% { transform: scale(1.0);}
            55% { transform: scale(1.3);}
            60%{transform: scale(1.0);}
            70%{transform: scale(1.3);}
            80%{transform: scale(1.0);}

        }
        
    }

    .react {
        color: white;
        margin-left: 150px;
        transition: all 0.8s ease;
        background-color: white;
        color: lightcoral;
        padding: 20px;
        border-radius: 10px;

        @include mobile {
            margin: 0;
            font-size: 8px;
        }

        &:hover{
            transform: scale(1.2);
        }
    }
}