@import "../global.scss";

.works {
  display: flex;
  background-color: white;
  justify-content: center;
  align-items: center;

  @include mobile {
    flex-direction: column;
  }

  @include mobile2 {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .left {
    flex: 0.3;
    display: flex;
    flex-direction: column;

    @include mobile {
      flex: 0.9;
    };

    @include mobile2 {
      flex: 2.5;
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }

    .imagens {
      width: 100%;
      overflow: hidden;
      object-fit: cover;

      img {
        width: 100%;
        overflow: hidden;
      }

      @include mobile2 {
        width: 80%;
        margin-left: 20px;
      }
      
    }

    .redesSociais{
        cursor: pointer;
        margin-top: 40px;
        display: flex;
        justify-content: center;
        align-items: center;

        @include mobile {
          height: 100px;
        }
        

        .MuiSvgIcon-root{
            font-size: 50px;
            margin-right: 20px;
            color:#fa3243;

            @include mobile {
              margin-right: 5px;
            }

            @include mobile2 {
              font-size: 35px;
              margin-left: 10px;
            }

            &:hover{
                color: #bd656c
            }
        }
    }
  }

  .right {
    flex: 0.7;
    display: flex;
    flex-direction: column;
    margin-right: 30px;

    @include mobile{
      flex: 0.1;
      overflow: hidden;
    }

    .sobreMim {
        display: flex;
        justify-content: center;
        align-items: center;

      .sobre {
        
        width: 400px;

        h1 {
          margin-bottom: 10px;
          color: #fa3243;
        }

        p,
        h4 {
          margin-top: 10px;
        }

        @include mobile {
          display: none;
        };

      }

      .tecnologias {
          width: 500px;

        @include mobile {
          display: none;
        }

          .programacao {

            li{
                margin-top: 10px;
                cursor: default;
            }
          }

          .design{
              margin-top: 20px;

             li {
                  margin-top: 10px;
                  cursor: default;
              }

            
              

          }
      }
    }
  }
}
