
@import "../global.scss";



.intro {
  background-color: white;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  @include mobile2 {
    flex-direction: row;
    overflow: hidden;
  }

  .left {
    flex: 0.3;

    .imgContainer {
      width: 700px;
      height: 700px;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      float: right;

      @include mobile {
        align-items: flex-start;
        height: 100%;
      }

      img {
        height: 80%;
        border-radius: 50%;

        @include mobile{
          height: 100%;
        }

        @include mobile2 {
          height: 300px;
          margin-left: 400px;
        }
      }
    }
  }

  .right {
    flex: 0.7;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;

      @include mobile{
        padding-left: 0;
        align-items: center;
      }


      .botao {
        width: 100px;
        height: 50px;
        margin-top: 30px;
        background-color: black;
        border: none;
        border-radius: 10px;
        transform-origin: center;
        transition: all 0.3s ease;
        
        @include mobile2 {
          height: 20px;
        }

        &:hover {
            transform: scale(1.3)
        }


        a {
            position: inherit;
            color: white;
        }
      }

      h1 {
        font-size: 60px;
        margin: 10px 0;

        @include mobile {
          font-size: 40px;
        }

      }
      h2 {
        font-size: 35px;

        @include mobile {
          font-size: 28px;
        }
      }
      h3 {
        font-size: 30px;

        @include mobile {
          font-size: 20px;
        }

        span {
          font-size: inherit;
          color: #fa3243;
        }
      }

      a {
        text-decoration: none;
        color: black;
        position: absolute;
        left: 40%;
        top: 750px;

        .MuiSvgIcon-root {
          font-size: 5rem;

          @include mobile {
            display: none;
          }
        }
      }

      p {
        margin-top: 20px;
        width: 45%;
        color: rgb(70, 68, 68);

        @include mobile {
          width: 80%;
          font-size: 15px;
        }
      }
    }
  }
}
