@import "../global.scss";

.topbar {
  width: 100%;
  height: 70px;
  background-color: white;
  color: #FA3243;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 1s ease;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile{
      justify-content: space-between;
    }

    .left {
      display: flex;
      align-items: center;

      @include mobile{
        margin-left: -40px;
      }

      .logo {
        font-size: 40px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;

        @include mobile {
         display: none;
        }
      }

      .itemContainer {
        display: flex;
        align-items: center;
        margin-left: 30px;

        

        .icon {
          font-size: 18px;
          margin-right: 5px;

          @include mobile {
            margin-right: 10px;
          }
        }

        span {
          font-size: 15px;
          font-weight: 500;
          margin-left: 10px;

          @include mobile {
            font-size: 15px;
          }
        }
      }
    }

    .right {
      .linhas {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span {
          width: 100%;
          height: 3px;
          background-color: #FA3243;
          transform-origin: left;
          transition: all 2s ease;
        }
      }
    }
  }

  &.active{
      background-color: #FA3243;
      color: white;

      .linhas{
          span{
              &:first-child{
                  background-color: white;
                  transform: rotate(45deg);
              }
              &:nth-child(2){
                  opacity: 0;
              }
              &:last-child{
                  background-color: white;
                  transform: rotate(-45deg);
              }
          }
      }
  }
}